import React, { useEffect, useState } from 'react'
import Iframe from 'react-iframe';
import ReactPlayer from 'react-player'
import Vimeo from '@u-wave/react-vimeo';
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP } from '../../utils'
import { NO_CAMERA_IMAGE } from '../../constants'
import AudioPlayer from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';


const VideoImageRender = ({ person, videoChange, newApi }) => {
  const [newImage, setNewImage] = useState('');
  const [updateNewVideo, setUpdateNewVideo] = useState('');
  useEffect(() => {
    setNewImage(person.filename);
    setUpdateNewVideo(person.mediaUrl);
  }, [])
  
  if ((person.type === 3) || (person.type === 5)) {

    if (person.link.indexOf("facebook") >= 0) return <div className='tb_video_holder'>
      <ReactPlayer onEnded={videoChange} className="tb_vid_player" poster={newImage} url={`${person.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} muted playsinline playing style={{ backgroundImage: 'url(' + newImage + ')' }} config={{facebook: { appId: '117029975354072' }}} />
      </div>
    else if (person.link.indexOf("vimeo") >= 0) return <div className='tb_video_holder'><Vimeo onEnd={videoChange} className="tb_vimeo_vid00" video={`${person.mediaUrl}&mute=1`} autoplay /></div>
    else if ((person.networkId && person.networkId == 30) && person.link.indexOf("vk") >= 0) return <div className='tb_video_holder'><Iframe className="iframeVideo youtubeVideoPlayer init" url={`${person.mediaUrl}&autoplay=1&mute=1`} width="500" height="600" allowFullScreen={true}></Iframe></div>
    else if (person.link.indexOf("youtube") >= 0 || person.link.indexOf("youtu.be") >= 0 || person.link.indexOf("soundcloud") >= 0 ||
      person.link.indexOf("tumblr") >= 0 || person.link.indexOf("linkedin") >= 0 || person.link.indexOf("workplace") >= 0) return <div className='tb_video_holder'><Iframe className="iframeVideo youtubeVideoPlayer init" url={`${person.mediaUrl}&mute=1`} width="500" height="600" allowFullScreen={true}>
      </Iframe></div>
    // else if (person.link.indexOf("instagram") >= 0) return <div className="tb_media_holder">
    //   <div className="tb_blur_bg" style={{ backgroundImage: 'url(' + newImage + ')' }}></div>
    //   <div className="tb_img_holder">
    //     <img className="tb-detail-image" src={newImage} alt="" height="1000" width="1000" />
    //   </div>
    // </div>
    else if (person && person.isAudio) return <div className='tb_video_holder'>
        <div className="tb_post_modal_audio_wrapper">
          <AudioPlayer
            className="tb_post_modal_audio"
            autoPlay={true}
            showJumpControls={false}
            showDownloadProgress={false}
            showFilledProgress={false}
            hasDefaultKeyBindings={false}
            layout='horizontal-reverse'
            customVolumeControls={[]}
            customAdditionalControls={[]}
            src={person.mediaUrl}
          />
       </div>
       {/* </div> */}
    </div>
    else return <div className='tb_video_holder'>
      <video onEnded={videoChange} playsInline className="iframeVideo youtubeVideoPlayer init" src={updateNewVideo} autoPlay="autoPlay" muted onLoad={e => setUpdateNewVideo(e.target.src)} onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }}
        poster={newImage}
        data-filter-id={person.filterId}
        data-type="video"
        data-stories={person.stories}
        data-network={person.networkId}
        data-postid={person.id}
        data-link={person.link}
        data-load={0}
        data-tb={newApi.UserDetail.db_table}
        data-wall-id={newApi.Wall.id} data-owner-id={newApi.Wall.owner} data-item-id={person.id}
      >
    </video>
    </div>
  }else{
    return false
  }

}

export default VideoImageRender
