import React, { PureComponent } from "react";

const PostNetworkChildIconView = ({  network, iconClass, fontAwesomeIcon, netwrk, themeRule }) => <div className="postNetwork" style={{ color : themeRule.iconType === 1 ? themeRule.iconColor : network.color, borderBottomColor:themeRule.iconColor}} data-network={network.name}> 
{fontAwesomeIcon !== null && netwrk !== "google-new" && netwrk !== "giphy" ? <i className={iconClass} style={{ color : themeRule.iconType === 1 ? themeRule.iconColor : network.color, borderBottomColor:themeRule.iconColor}}>
    {/* <FontAwesomeIcon icon={fontAwesomeIcon} /> */}
</i> :netwrk === "google-new"?<i className={`fa fa-google`}/>:netwrk === "giphy"?<i className={`fa fa-giphy`}/>:netwrk === "RSS"?<i className={`fa fa-rss`}/>: <i className={iconClass}></i>}


</div>


export default class NetworkIcon extends PureComponent {

    render() {
        const { activeCardId, network, themeRule} = this.props;
        let netwrk = network.icon !== null ? network.icon.substring(3) : null;
        return (
            <React.Fragment>{
                network.icon !== null ?
                        network.name !== "Workplace" && network.name !== "SMS" ? 
                        netwrk == "twitter" ? <svg id="twitter" xmlns="http://www.w3.org/2000/svg" width="26" height="26" viewBox="0 0 44.518 44.518">
                        <path id="Path_5" data-name="Path 5" d="M0,0H44.518V44.518H0Z" fill="none"/>
                        <path id="Path_7" data-name="Path 7" d="M19.036,13.554,30.942,0H28.121L17.778,11.766,9.523,0H0L12.486,17.794,0,32.006H2.821L13.737,19.578l8.72,12.429H31.98Zm-3.865,4.4L13.9,16.177,3.838,2.083H8.172L16.3,13.462l1.262,1.773L28.12,30.025H23.786Zm0,0" transform="translate(6.313 6.281)" fill="#0f1419"/>
                      </svg> :
                        <PostNetworkChildIconView themeRule={themeRule} network={network} iconClass={network.id==20 ? `tb_icon fa fa-${netwrk}` : `fa fa-${netwrk}`} fontAwesomeIcon={['fab', netwrk === 'facebook' ? 'facebook-f' : netwrk]} netwrk={netwrk} />
                            : network.name === "Workplace" ? <PostNetworkChildIconView themeRule={themeRule} network={network} iconClass={`fa icon-taggbox-workplace-2`} fontAwesomeIcon={null} netwrk={netwrk} />
                                : network.name === "SMS" ? <PostNetworkChildIconView themeRule={themeRule} network={network} iconClass={`fa fa-envelope-o`} fontAwesomeIcon={['far', 'envelope']} netwrk={netwrk} />
                                    : <PostNetworkChildIconView themeRule={themeRule} network={network} iconClass={network.id==20 ? `tb_icon fa fa-${netwrk}` : `fa fa-${netwrk}`} fontAwesomeIcon={['fas', 'rss']} netwrk={netwrk} />
                        : null 

            }</React.Fragment>
        )
    }
}


