import React, { Component } from 'react';
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT } from '../../utils';
import { NO_CAMERA_IMAGE } from '../../constants';

const mediaDimension = JSON.parse(window.localStorage.getItem('mediaDimension'));
class PopupImage extends Component {
    constructor(props) {
        super(props);
        this.state = {
          mediaLoaded:false,
          mediaSize:{
            height:mediaDimension != null ? mediaDimension[0] : 650,
            width:mediaDimension != null ? mediaDimension[1] : 650,
          },
          imgUrl:this.props.popUpData.filename
        }
        this.mediaRef = React.createRef();
    }
    componentDidMount() {
        window.addEventListener('resize', this.updateImageDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateImageDimensions);
    }
    
    updateImageDimensions = () => {
        if (this.mediaRef.current) {
        if (this.mediaRef.current.getAttribute("data-load") === 1) { 
            this.setState({ imgUrl: NO_CAMERA_IMAGE })
        }
        const mediaDimensionUpdate = [this.mediaRef.current.offsetHeight, this.mediaRef.current.offsetWidth];
        window.localStorage.setItem('mediaDimension', JSON.stringify(mediaDimensionUpdate));
        this.setState({ 
            mediaLoaded:true,
            mediaSize:{
                height:this.mediaRef.current.offsetHeight,
                width:this.mediaRef.current.offsetWidth
            }},()=>this.props.mediaSizeCallback({
                height: this.state.mediaSize.height,
                width: this.state.mediaSize.width
            }));
        }
    };


    render() {
        const { mediaLoaded, mediaSize, imgUrl } = this.state
        const { popUpData, newApi } = this.props
        const ImageHeight =  (popUpData.mediaHeight < 602 && popUpData.mediaWidth < 602) ? popUpData.mediaHeight*1.5 : popUpData.mediaHeight;
        const ImageWidth =  (popUpData.mediaHeight < 602 && popUpData.mediaWidth < 602) ? popUpData.mediaWidth*1.5 : popUpData.mediaWidth;
        return (
            <div className={`tb_img_holder media_holder${popUpData.id}`}>
                <img ref={this.mediaRef} className="tb-detail-image" src={imgUrl} height={ImageHeight} width={ImageWidth} alt=""
                data-load="0" data-network={popUpData.networkId} data-postid={popUpData.id} data-link={popUpData.link}
                data-wall-id={newApi.wall.Wall.id} data-owner-id={newApi.wall.Wall.owner} data-item-id={popUpData.id}
                data-tb={newApi.wall.UserDetail.db_table}
                data-filter-id={popUpData.filterId}
                data-stories={popUpData.stories}
                onLoad={this.updateImageDimensions}
                onError={(e) => {
                    IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)

                }} 
                style={{ opacity : (popUpData.type === 2) || (popUpData.type === 4 ) ? 1 : 0 }} />
            </div>
        );
    }
}

export default PopupImage;