import React from "react";
import ReactHtmlParser from 'react-html-parser';
import Emojify from 'react-emojione'; 
export const convertEmoji = (data, signageTheme) => {
    return signageTheme === 46 ? <Emojify style={{height: 22, width: 22}}>{ReactHtmlParser(data)}</Emojify> : ReactHtmlParser(data)
  }
export const FontFamilyH1Heading = ({ styles, contentTitle }) => contentTitle.length > 0 ? <h1 style={styles}>
    {convertEmoji(contentTitle)}
</h1> : null

export const FontLeftQuote = ({ styles }) => <span className="leftquote" style={styles}> <i className="fa fa-quote-left" aria-hidden="true"></i> </span>
export const FontRightQuote = ({ styles }) => <span className="rightquote" style={styles}> <i className="fa fa-quote-right" aria-hidden="true"></i> </span>
export const FontFamilyUpperEmojiApplied = ({ styles, content }) => content.length > 0 ? <p className="emojiApplied"><FontLeftQuote styles={styles} />{content}<FontRightQuote styles={styles} /></p> : null